:root {
  --main-bg-color: #1700c7;
}

::selection {
  background: rgba(255, 255, 255, 0.99);
  color: var(--main-bg-color);
}

::-moz-selection {
  background: rgba(255, 255, 255, 0.99);
  color: var(--main-bg-color);
}

* {
  box-sizing: border-box;
}

body {
  font-size: 2rem;
  padding: 30px;
  font-family: 'Main', sans-serif;
  background-color: var(--main-bg-color);
  color: #fff;
}

.all {
	position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}

.secret-link {
  cursor: grabbing;
  text-decoration: line-through;
}

a,
.secret-link {
	color: #fff;
	padding-right: 16px;
	background: url("data:image/svg+xml;base64,") right center no-repeat;
	background-size: 14px;
}

h3 a {
	padding-right: 18px;
	background-size: 16px;
}

hr {
  border: 0.5px solid #fff;
}

header {
  height: 88vh;
  position: relative;
  width: auto;
}

br {
  display: none;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
	word-spacing: 28px;
  position: absolute;
  bottom: 0;
  left: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

h1 span {
  padding-bottom: 2px;
  border-bottom: 5px solid #fff;
}

h2 {
  margin-top: 120px;
}

h3 {
  margin-bottom: 3px;
}

h4 {
  margin-bottom: 15px;
}

h4::before {
  content: '(';
}

h4::after {
  content: ')';
}

ul {
  margin: 0;
  list-style-type: square;
  padding: 0 1rem;
}

ul li {
  margin: 5px 0;
	margin-left: -15px;
}

section ul li {
  margin-bottom: 20px;
}

section ul.tight li {
  margin-bottom: 10px;
}

ul li sup {
  margin-right: 5px;
  font-size: 0.6em;
}

/* ul li::before {
	content: 'â–  ';
} */

section, header {
  margin-bottom: 50px;
}

section ul {
  margin-top: 10px;
}

.smaller {
  font-size: 0.8em;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 0.9rem;
  }

  header {
    height: 40vh;
  }

  br {
    display: block;
  }

	h1 {
		word-spacing: 18px;
	}

  h1 span {
    padding-bottom: 2px;
    border-bottom: 2px solid #fff;
  }
}

footer {
	margin: 100px 0 200px;
	padding-bottom: 300px;
}

@media only screen and (max-width: 767px) {
	footer {
		padding-bottom: 100px;
	}
}

@font-face {
    font-family: 'Main';
    src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
    font-weight: normal;
    font-style: normal;
}
